* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto' 'Segoe';
}

body {
  height: 100vh;
}

.sent-rep {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.sent-text {
  margin: 2% 0 2% 2%;
  font-size: 3rem;
  text-align: left;
  color: rgba(255, 215, 0, 0.85)
}

.graphs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.pol-label,
.em-label {
  font-size: 2rem;
  margin-bottom: 3%;
}

.sent-polarity h3,
.sent-emotions h3 {
  font-size: 2rem;
  margin-bottom: 2%;
}

.content-enter {
  opacity: 0;
}

.content-enter.content-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-out;
  -o-transition: opacity 500ms ease-out;
  transition: opacity 500ms ease-out;
}

.content-leave {
  opacity: 1;
}

.content-leave.content-leave-active {
  opacity: 0;
  -webkit-transition: opacity 1s ease-in;
  -o-transition: opacity 1s ease-in;
  transition: opacity 1s ease-in;
}

.content-appear {
  opacity: 0;
}

.content-appear.content-appear-active {
  opacity: 1;
  -webkit-transition: opacity 0.750s ease;
  -o-transition: opacity 0.750s ease;
  transition: opacity 0.750s ease;
}

